<template>
<div class='seller-wrapper' ref='sellerWrapper'>
  <div class="seller-content">
    <div class="info">
      <div class="title">
        <div class="text">{{this.datas.seller.name}}</div>
        <div class="star-wrapper">
          <star :score='datas.seller.score' :size='36'></star>
          <span class="rate-count">({{this.datas.seller.ratingCount}})</span>
          <span class="sell-count">月售{{this.datas.seller.sellCount}}单</span>
        </div>
        <div class="collect" @click='show=!show'>
          <span class="icon-favorite" :class='{active: show}'></span>
          <span class="text">{{show?'已收藏':'收藏'}}</span>
        </div>
      </div>
      <div class="remark">
        <div class="block">
          <h2>起送价</h2>
          <div class="content">
            <span class="num">{{datas.seller.minPrice}}元</span>
          </div>
        </div>
        <div class="block">
          <h2>商家配送</h2>
          <div class="content">
            <span class="num">{{datas.seller.deliveryPrice}}元</span>
          </div>
        </div>
        <div class="block">
          <h2>平局配送时间</h2>
          <div class="content">
            <span class="num">{{datas.seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
    </div>
    <div class="divider"></div>
    <div class="activities">
      <div class="bulletin">
        <h1>公告与活动</h1>
        <div class="content">{{this.datas.seller.bulletin}}</div>
      </div>
    </div>
    <div class="supports">
      <ul>
        <li class="item" v-for='(value,index) in datas.seller.supports' :key='index'>
          <span class="iconMap" :class='icon[value.type]'></span>
          <span class="text">{{value.description}}</span>
        </li>
      </ul>
    </div>
    <div class="seller-imgs" >
      <h1>商家实景</h1>
      <div class="img-wrapper" ref='imgWrapper'>
        <div ref='piclist' class='piclist'>
          <img :src="value" alt="" v-for='(value,index) in datas.seller.pics' :key='index' width="120" height="90">
        </div>
      </div>
    </div>
    <div class="divider"></div>
    <div class="seller-info">
      <h1>商家信息</h1>
      <ul class='info-list'>
        <li class="info" v-for='(value,index) in  datas.seller.infos' :key='index'>{{value}}</li>
      </ul>
    </div>
  </div>
</div>
</template>
<script>
import icon from './mods/icon.js'
import star from './mods/star.vue'
import Scroll from 'better-scroll'
export default {
  props: ['datas'],
  components: {
    star
  },
  data () {
    return {
      show: false,
      icon
    }
  },
  mounted () {
    this.inital()
    /* eslint-disable no-new */
    new Scroll(this.$refs.sellerWrapper, {click: true})
    this.$nextTick(() => {
      new Scroll(this.$refs.imgWrapper, {click: true, scrollX: true, scrollY: false, startX: 0, eventPassthrough: 'vertical'})
    })
  },
  methods: {
    inital () {
      let width = this.datas.seller.pics.length * 120
      this.$refs.piclist.style.width = width + 'px'
    }
  }
}
</script>
<style></style>



