<template>
<div class="goods">
    <div class="menu-wrapper" ref='menuwrapper'>
      <ul>
        <li class="menu-item" v-for='(item,index) in goods' :key='index'>
          <span class="text"><span class="iconMap" v-show='item.type>0' :class='icon[item.type]'></span>{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref='foodwrapper'>
      <ul>
        <li class="food-list food-list-hook" v-for='(item,index) in goods' :key='index'>
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li class="food-item" v-for='(value,index) in item.foods' :key='index' @click='clicktarget(value)' >
              <div class="icon">
                <img :src="value.icon" alt="" width="57" height="57">
              </div>
              <div class="content">
                <h2 class="name">{{value.name}}</h2>
                <p class="description">{{value.description}}</p>
                <div class="sell-info">
                  <span class="sellCount">月售{{value.sellCount}}份</span>
                  <span class="rating">好评率{{value.rating}}%</span>
                </div>
                <div class="price">
                  <span class="newPrice">
                    <span class="unit">$</span>
                    {{value.price}}元
                  </span>
                  <span></span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :number='value'></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcar :buystate='buystate' :deliveryPrice='datas.seller.deliveryPrice' :minPrice='datas.seller.minPrice'></shopcar>
    <foodDetail ref='foodDetail' :detail='specific'></foodDetail>
</div>
</template>
<script>
import icon from './mods/icon.js'
import Scroll from 'better-scroll'
import shopcar from './mods/shopcar.vue'
import cartcontrol from './mods/shopbtn.vue'
import foodDetail from './mods/fooddetail.vue'
export default {
  props: ['datas'],
  components: {
    shopcar,
    cartcontrol,
    foodDetail
  },
  computed: {
    buystate () {
      let arr = []
      this.goods.forEach((value, index) => {
        value.foods.forEach((val, ind) => {
          if (val.count > 0) {
            arr.push(val)
          }
        })
      })
      return arr
    }
  },
  data () {
    return {
      goods: this.datas.goods,
      icon,
      specific: {}
    }
  },
  mounted () {
    this.$nextTick(() => {
      /* eslint-disable no-new */
      new Scroll(this.$refs['foodwrapper'], {click: true})
      new Scroll(this.$refs['menuwrapper'], {click: true})
    })
  },
  methods: {
    scroll () {
      /* eslint-disable no-new */
      new Scroll(this.$refs['foodwrapper'])
    },
    clicktarget (food) {
      this.$refs.foodDetail.showdetail()
      this.specific = food
    }
  }
}
</script>
<style></style>



