<template>
<div class="shopCart">
  <div class="content"  @click='scroll'>
    <div class="content-left">
      <div class="logo-wrapper">
        <div class="badge" v-show='totalprice>0'></div>
        <div class="logo active">
          <i class="icon-shopping_cart"></i>
        </div>
        <div class="badge" v-show='totalprice>0'>{{totalnum}}</div>
      </div>
      <div class="price" :class='{"active":totalprice}'>￥{{totalprice}}</div>
      <div class="desc" v-show='totalprice<100'>另需要配送费￥{{deliveryPrice}}元</div>
    </div>
    <div class="content-right" :class='{"enough":totalprice>=20}'>
      <div class="pay" >{{difference}}</div>
    </div>
  </div>
  <transition name='transHeight'>
    <div class="shopcart-list" v-show='totalprice>0&&show' >
      <div class="list-header">
        <h1 class="title">购物车</h1>
        <span class="empty" @click='clear'>清空</span>
      </div>
      <div class="list-content" ref='shoplist'>
        <ul>
          <li class="food" v-for='(item,index) in buystate' :key='index'>
            <span class="name">{{item.name}}</span>
            <div class="price">
              <span>￥{{item.price}}</span>
            </div>
            <div class="cartcontrol-wrapper">
              <cartcontrol :number='item'></cartcontrol>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </transition>
</div>
</template>
<script>
import Scroll from 'better-scroll'
import cartcontrol from './shopbtn.vue'
export default {
  props: ['deliveryPrice', 'minPrice', 'buystate'],
  data () {
    return {
      show: false
    }
  },
  components: {
    cartcontrol
  },
  computed: {
    totalprice () {
      let totalprice = 0
      this.buystate.forEach((value, index) => {
        totalprice += value.price * value.count
      })
      return totalprice
    },
    difference () {
      let diff = this.minPrice - this.totalprice
      if (diff === 20) {
        return `$${diff}元起送`
      } else if (diff > 0) {
        return `还差￥${diff}元`
      } else {
        return `去结算`
      }
    },
    totalnum () {
      let num = 0
      this.buystate.forEach((value, index) => {
        num += value.count
      })
      return num
    }
  },
  methods: {
    clear () {
      this.buystate.forEach((value, index) => {
        value.count = 0
      })
      this.show = false
    },
    scroll () {
      this.show = !this.show
      this.$nextTick(() => {
        /* eslint-disable no-new */
        new Scroll(this.$refs['shoplist'], {click: true})
      })
    }
  }
}
</script>
<style></style>



