<template>
<div class="ratingsWrapper" ref='ratingsWrapper'>
  <div class="ratings-content">
    <div class="info">
      <div class="mark">
        <div class="num">{{datas.seller.score}}</div>
        <div class="text">综合评分</div>
        <div class="contrast">高于周边商家{{datas.seller.rankRate}}</div>
      </div>
      <div class="stars">
        <div class="serviceScore">
          <span class="text">服务态度</span>
          <star :score='datas.seller.serviceScore' :size='36'></star>
          <span class="num">{{datas.seller.serviceScore}}</span>
        </div>
        <div class="foodScore">
          <span class="text">商品评分</span>
          <star :score='datas.seller.foodScore' :size='36'></star>
          <span class="num">{{datas.seller.foodScore}}</span>
        </div>
        <div class="deliveryTime">
          预计送达时间{{datas.seller.deliveryTime}}分钟
        </div>
      </div>
    </div>
    <div class="divider"></div>
    <div class="evaluation">
      <div class="classify">
        <span class="item" v-for='(value,index) in classs' :key='index' :class='{active: value.active, bad: index==2, badActive:value.active&&index==2}' @click='changeclick(value)'>{{value.name}}{{value.num}}</span>
      </div>
      <div class="switch">
        <span class="icon-check_circle" @click='flag=!flag' :class='{on:flag}'></span>
        <span class="text">只看有内容的评价</span>
      </div>
      <div class="evel-list">
        <ul>
            <li class="evel" v-for='(value,index) in filter' :key='index'>
              <div class="avatar">
                <img :src="value.avatar" alt="" width="28" height="28">
              </div>
              <div class="content">
                <div class="user">
                  <span class="name">{{value.name}}</span>
                  <span class="rateTime">{{new Date(value.rateTime).toLocaleString()}}</span>
                </div>
                <div class="star-wrapper">
                  <star :score='value.score' :size='24'></star>
                  <span class="deliveryTime">{{value.deliveryTime}}分钟送达</span>
                </div>
                <div class="text">{{value.text}}</div>
                <div class="recommend">
                  <span class="icon" :class='{"icon-thumb_up": !value.rateType}'></span>
                  <span class="dish" v-for='(a,b) in value.recommend' :key='b'>{{a}}</span>
                </div>
              </div>
            </li>
          </ul>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import axios from 'axios'
import Scroll from 'better-scroll'
import star from './mods/star.vue'
export default {
  props: ['datas'],
  components: {
    star
  },
  computed: {
    filter () {
      let arr = []
      if (this.flag) {
        this.comments.forEach(value => {
          if (value.text.length > 0) {
            arr.push(value)
          }
        })
        return arr
      } else {
        return this.comments
      }
    }
  },
  data () {
    return {
      data: {
        ratings: {}
      },
      classs: [
        {name: '全部', active: true, num: 0, comment: []},
        {name: '推荐', active: false, num: 0, comment: []},
        {name: '吐槽', active: false, num: 0, comment: []}
      ],
      comments: [],
      flag: true
    }
  },
  mounted () {
    axios.get('./static/data.json').then(response => {
      this.data.ratings = response.data.ratings
      this.data.ratings.forEach(val => {
        this.classs[0].num++
        this.classs[0].comment.push(val)
        if (val.rateType === 1) {
          this.classs[2].num++
          this.classs[2].comment.push(val)
        } else {
          this.classs[1].num++
          this.classs[1].comment.push(val)
        }
      })
      this.comments = this.classs[0].comment
      this.$nextTick(() => {
        this.sc = new Scroll(this.$refs.ratingsWrapper, {click: true})
      })
    })
  },
  methods: {
    changeclick (obj) {
      this.classs.forEach(value => {
        value.active = false
      })
      obj.active = true
      this.comments = obj.comment
    }
  }
}
</script>
<style></style>



