<template>
<div class="star" :class='`star-${size}`'>
  <span class="star-item" v-for='(item,index) in star_num' :class='item' :key='index'></span>
</div>
</template>
<script>
export default {
  props: ['score', 'size'],
  computed: {
    star_num () {
      let int = Math.floor(this.score)
      let dec = (this.score - int) >= 0.5
      let arr = []
      for (let i = 0; i < int; i++) {
        arr.push('on')
      }
      if (dec) {
        arr.push('half')
      }
      while (arr.length < 5) {
        arr.push('off')
      }
      return arr
    }
  }
}
</script>
<style></style>



