<template>
<transition name='move'>
  <div class="detailWrapper" v-show='show' ref='detailWrapper'>
    <div class="foodDetail">
      <div class="back" @click='back'>
        <i class="icon-arrow_lift"></i>
      </div>
      <img :src='detail.image' alt="" width="100%" height="425">
      <div class="info">
        <div class="title">{{detail.name}}</div>
        <div class="desc">
          <span>月售{{detail.sellCount}}</span>
          <span>好评率{{detail.rating}}%</span>
        </div>
        <div class="price">
          <span class="unit">￥{{detail.price}}</span>
        </div>
        <transition name='fade'>
          <div class="shopCart" @click='add' v-show='detail.count === 0|| detail.count == undefined'>
            <div class="text">加入购物车</div>
          </div>
        </transition>
        <cartcontrol :number='detail' ref='details'></cartcontrol>
      </div>
      <div class="divider"></div>
      <div class="desc">
        <div class="title">商品介绍</div>
        <div class="content">{{detail.info}}</div>
      </div>
      <div class="divider"></div>
      <div class="evaluation">
        <div class="title">商品评价</div>
        <div class="classify">
          <span class="item" @click='changeactive(item)' v-for='(item,index) in classify' :key='index' :class='{active: item.active, bad: index === 2, badActive: item.active && index === 2}'>{{item.name}}{{item.num}}</span>
        </div>
        <div class="switch">
          <span class="icon-check_circle" @click='flag=!flag' :class='{on:flag}'></span>
          <span class="text">只看有内容的评价</span>
        </div>
        <div class="evel-list">
          <ul>
            <li class="evel" v-for='(value,index) in filter' :key='index'>
              <div class="userInfo">
                <div class="time">{{new Date(value.rateTime).toLocaleString()}}</div>
                <div class="user">
                  <span>{{value.username}}</span>
                  <span class="avatar">
                    <img :src="value.avatar" alt="" width="12" height="12">
                  </span>
                </div>
              </div>
              <div class="content">
                <span class="icon" :class="value.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                <span class="text">{{value.text}}</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>
<script>
import Scroll from 'better-scroll'
import cartcontrol from './shopbtn.vue'
export default {
  props: ['detail'],
  components: {
    cartcontrol
  },
  computed: {
    filter () {
      let arr = []
      if (this.flag) {
        this.choosecomment.forEach((value, index) => {
          if (value.text.length > 0) {
            arr.push(value)
          }
        })
        return arr
      } else {
        return this.choosecomment
      }
    }
  },
  data () {
    return {
      show: false,
      classify: [
        {name: '全部', num: 0, active: true, comment: []},
        {name: '推荐', num: 0, active: false, comment: []},
        {name: '吐槽', num: 0, active: false, comment: []}
      ],
      choosecomment: [],
      flag: true
    }
  },
  methods: {
    add () {
      this.$refs.details.add()
    },
    showdetail () {
      this.show = true
      this.$nextTick(() => {
        this.choosecomment = this.detail.ratings
        this.detail.ratings.forEach((value, index) => {
          this.classify[0].num++
          this.classify[0].comment.push(value)
          if (value.rateType === 1) {
            this.classify[2].comment.push(value)
            this.classify[2].num++
          } else {
            this.classify[1].comment.push(value)
            this.classify[1].num++
          }
        })
        if (!this.sc) {
          /* eslint-disable no-new */
          this.sc = new Scroll(this.$refs.detailWrapper, {click: true})
        }
      })
    },
    changeactive (obj) {
      this.classify.forEach((value, index) => {
        value.active = false
      })
      obj.active = true
      this.choosecomment = obj.comment
    },
    back () {
      this.show = !this.show
      this.classify.forEach((value, index) => {
        value.num = 0
        value.comment = []
        value.active = false
        if (index === 0) {
          value.active = true
        }
      })
    }
  }
}
</script>
<style></style>



