<template>
  <div id="app">
    <vheader :seller='datas.seller'></vheader>
    <div class="tab">
      <div class="tab-item">
        <router-link to='/goods'>商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to='/ratings'>评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to='./seller'>商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :datas='datas' ref='abc'></router-view>
    </keep-alive>
  </div>
</template>

<script>
import vheader from '@/components/mods/header.vue'
import axios from 'axios'
// import router from './router'
export default {
  name: 'App',
  data () {
    return {
      datas: {
        seller: {
          supports: [{
            description: ''
          }]
        },
        goods: []
      }
    }
  },
  components: {
    vheader
  },
  mounted () {
    axios.get('./static/data.json').then(response => {
      this.datas.seller = response.data.seller
      this.datas.goods = response.data.goods
      // this.$nextTick(() => {
      //   this.$refs.abc.scroll()
      // })
    })
  }
}
</script>

<style>
.tab > .tab-item > a.router-link-exact-active {
  color: red
}
</style>



