<template>
  <div class="cartcontrol">
    <transition name='fadeRotate'>
      <div class="cart-decrease" @click.stop='decrease' v-show='number.count>0'>
        <span class="icon-remove_circle_outline inner"></span>
      </div>
    </transition>
    <div class="cart-count" v-show='number.count>0'>{{number.count}}</div>
    <div id="add" class="cart-add" @click.stop='add'>
      <i class="icon-add_circle"></i>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import {mapMutations, mapState} from 'vuex'
export default {
  props: ['number'],
  methods: {
    add () {
      if (typeof this.number.count === 'undefined') {
        Vue.set(this.number, 'count', 0)
        Vue.set(this.number, 'active', true)
      }
      this.number.count++
      if (this.number.active) {
        this.vxadd(this.number)
        this.number.active = false
      }
    },
    decrease () {
      if (typeof this.number.count === 'undefined') {
        Vue.set(this.number, 'count', 0)
      }
      if (this.number.count >= 1) {
        this.number.count--
      }
      if (this.number.count === 0) {
        this.vxdecrease(this.number)
        this.number.active = true
      }
    },
    ...mapMutations([
      'vxadd', 'vxdecrease'
    ])
  },
  computed: {
    ...mapState([
      'vxfood'
    ])
  }
}
</script>
<style></style>



